import React from 'react';
import { allRegions } from '../calculation_data/allData.js'
import { ProgressBar } from 'react-bootstrap'
import './steps.css'


class Results extends React.Component {

    constructor(props) {
        super(props);
        // The data from the previous step comes in this.props.data

        this.state = {
            data: this.props.data,
        }
    }


    // Get correct region
    // Iterate over regions in allData
    // get parties from that region.
    getRegion(regions, region){
      let tempRegion
      Object.keys(regions).map(function(key, index) {
          if(key === region){
            tempRegion = key;
          }
        });
      console.log(tempRegion);
      return regions[tempRegion]
    }


    // For all parties calculate score
    scorePerParty(parties, result){
      let scores = []
      for (const [key, value] of Object.entries(parties)) {
        var scoreArray = this.compareScore(value, result);
        var totalScore = scoreArray.reduce((a, b) => a + b, 0);
        scores.push({key:key, totalScore:totalScore})
      }
      return scores
    }

    // Compare score
    compareScore(a, b){
      var c = a.map(function(e, i) {
            var arr = [e, b[i]];
            var diff = Math.abs(arr[0] - arr[1]);
            return diff
          });
      return c
    }

    // Sort on lowest difference for most suitable parties
    sortParties(partyScores){
      function compare(a,b) {
            if (a.totalScore < b.totalScore)
              return -1;
            if (a.totalScore > b.totalScore)
              return 1;
            return 0;
          }

      partyScores = partyScores.sort(compare);
      console.log(partyScores);
      return partyScores;
    }


    render() {
      console.log(allRegions)
      var currRegion = this.getRegion(allRegions, this.state.data.name)
      console.log(currRegion);
      const questions = this.state.data.answers.length
      const percentage = (100/this.state.data.answers);
      var allScores = this.scorePerParty(currRegion, this.state.data.answers)
      console.log(allScores);
      const sorted = this.sortParties(allScores);
      const now0 = ((questions - sorted[0].totalScore)/ questions) * 100;
      const now1 = ((questions - sorted[1].totalScore)/ questions) * 100;
      const now2 = ((questions - sorted[2].totalScore)/ questions) * 100;
        return (
          <div className="question-area">
            De partijen die het best bij u passen in regio {this.state.data.name} zijn:
            <div className="top3container"><p className="top3">{sorted[0].key}</p>
            <ProgressBar bsStyle="success" now={now0} label={`${Math.round(now0)}%`} />
            </div>
            <div><p className="top3">{sorted[1].key}</p>
            <ProgressBar bsStyle="success" now={now1}  label={`${Math.round(now1)}%`} />
            </div>
            <div><p className="top3">{sorted[2].key}</p>
            <ProgressBar bsStyle="success" now={now2}   label={`${Math.round(now2)}%`}/></div>
          </div>
        );
    }
}

export default Results;
