import React from 'react';
import './steps.css'
import { Button } from 'react-bootstrap';

class StepTwoForm extends React.Component {

    constructor(props) {
        super(props);
        this.handleOnNextStep = this.handleOnNextStep.bind(this);
        this.handleOnPreviousStep = this.handleOnPreviousStep.bind(this);

        // The data from the previous step comes in this.props.data

        this.state = {
            data: this.props.data,
        }
    }

    handleOnNextStep() {
        let data = this.state.data;
        this.props.nextStep(data);
    }

    handleOnPreviousStep() {
        let data = this.state.data;
        this.props.previousStep(data);
    }

    render() {
      console.log(this.props.data.name);
        return (
          <div>
            <div className="step2Container">
              <span><i class="material-icons" onClick={this.handleOnPreviousStep}>
                keyboard_backspace
              </i> <span style={{ paddingLeft: "10px"}}>Uw regio is {this.props.data.name} </span></span>
            <p style={{ paddingTop: "10px"}}> U kunt nu uw mening geven over 19 stellingen. <br/> Op basis van de antwoorden die partijen in uw regio gaven wordt er berekend welke het beste bij u past!</p>
            </div>
            <div className="startButton">
            <Button  bsSize={"large"}
                    type="button"
                    onClick={this.handleOnNextStep}
                    className="">Start de Kieswijzer
                  </Button>
          </div>
          </div>
        );
    }
}

export default StepTwoForm;
