import React from 'react';
import './steps.css'
import { Button } from 'react-bootstrap';

class StepFourForm extends React.Component {

  constructor(props) {
      super(props);
      this.handleOnPreviousStep = this.handleOnPreviousStep.bind(this);
      this.handleEens = this.handleEens.bind(this);
      this.handleNeutraal = this.handleNeutraal.bind(this);
      this.handleOneens = this.handleOneens.bind(this);
      this.handleZeerEens = this.handleZeerEens.bind(this);
      this.handleZeerOneens = this.handleZeerOneens.bind(this);

      // The data from the previous step comes in this.props.data
      this.state = {
          data: this.props.data,
      }
  }

  handleZeerEens(){
    let tempData = this.state.data;
    tempData.answers = [...tempData.answers, 2]
    this.props.nextStep(tempData);
  }

  handleEens(){
    let tempData = this.state.data;
    tempData.answers = [...tempData.answers, 1]
    this.props.nextStep(tempData);
  }

  handleNeutraal(){
    let tempData = this.state.data;
    tempData.answers = [...tempData.answers, 0]
    this.props.nextStep(tempData);
  }

  handleOneens(){
    let tempData = this.state.data;
    tempData.answers = [...tempData.answers, -1]
    this.props.nextStep(tempData);
  }

  handleZeerOneens(){
    let tempData = this.state.data;
    tempData.answers = [...tempData.answers, -2]
    this.props.nextStep(tempData);
  }


  handleOnPreviousStep() {
      let data = this.state.data;
      this.props.previousStep(data);
  }


  render() {
    console.log(this.props.data.name);
      return (
        <div className="stepContainer">
          <div className="category">
          <div className="inner-category">
            <h3>Energietransitie </h3>
            <p>De energietransitie houdt in dat energie wordt opgewekt uit hernieuwbare bronnen zoals wind,
water en zon. Ook betekent het dat er zuinig met energie wordt omgegaan. ‚ÄòEnergie neutraal‚Äô
betekent dat een waterschap de energie die wordt gebruikt ook zelf opwekt. </p>
          </div>
          </div>
        <div className="question-area">
          <div className="question">2. Energieneutraal in 2025 is te laat, het streven moet zijn energieneutraal in 2023.</div>
          <div className="KieswijzerButtons">
          <div style={{padding: '8px'}}><Button onClick={this.handleEens} className="KieswijzerButton">Eens</Button></div>
          <div style={{padding: '8px'}}><Button onClick={this.handleNeutraal} className="KieswijzerButton">Neutraal</Button></div>
          <div style={{padding: '8px'}}><Button onClick={this.handleOneens}className="KieswijzerButton">Oneens</Button></div>
          </div>
        </div>
        <div className="placeholder"></div>
      </div>
      );
  }
}

export default StepFourForm;
