import React from 'react';
import './steps.css'
import { Button } from 'react-bootstrap';

class Bedankt extends React.Component {

    constructor(props) {
        super(props);
        this.handleOnNextStep = this.handleOnNextStep.bind(this);
        this.handleOnPreviousStep = this.handleOnPreviousStep.bind(this);

        // The data from the previous step comes in this.props.data

        this.state = {
            data: this.props.data,
        }
    }

    handleOnNextStep() {
        let data = this.state.data;
        this.props.nextStep(data);
    }

    handleOnPreviousStep() {
        let data = this.state.data;
        this.props.previousStep(data);
    }

    render() {
      console.log(this.props.data.name);
        return (
          <div>
            <div className="step2Container">
            <p style={{ paddingTop: "10px"}}> Bedankt! <br/> Bekijk uw resultaten op de volgende pagina</p>
            </div>
            <div className="startButton">
            <Button  bsSize={"large"}
                    type="button"
                    onClick={this.handleOnNextStep}
                    className="">Resultaten
                  </Button>
          </div>
          </div>
        );
    }
}

export default Bedankt;
